import React, { Component } from 'react';
import PropTypes from 'prop-types';
import AnchorControl from './AnchorControl';
import {
    ANCHOR_CONTROL_CONTENT_MODE
    } from '../../../../../utilities/src/affiliate_const';

export default class PopupAnchorControl extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const rakutenImageStyle ={
            float:"left",
            maxHeight:"27px",
            width:"auto",
            marginTop:"5px",
        }
        const rakutenImageStyleSmall ={
            float:"left",
            maxHeight:"27px",
            width:"auto",
            marginTop:"0px",
        }
        const imageTDSmallStyle = {
            margin:"15px",
        }
        const imageTDTinyStyle = {
            margin:"10px",
        }
        const previewRakutenDiv = {
            float: "right",
            width: "50%",
            height: "32px",
            backgroundColor: "#bf0000",
            color: "#fff !important",
            fontSize: "14px",
            fontWeight: "500",
            lineHeight: "32px",
            marginLeft: "1px",
            padding: "0 12px",
            borderRadius: "16px",
            cursor: "pointer",
            textAlign: "center",
        }
        const previewRakutenDivSmall = {
            float: "right",
            width: "41%",
            height: "27px",
            backgroundColor: "#bf0000",
            color: "#fff !important",
            fontSize: "12px",
            fontWeight: "500",
            lineHeight: "27px",
            marginLeft: "1px",
            padding: "0 12px",
            borderRadius: "16px",
            cursor: "pointer",
            textAlign: "center",
        }

        return (
            <AnchorControl
                url_url={this.props.url_url}
                url_style={this.props.url_style}
                url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.CHILDREN}
                >
                <div style={this.props.image_size==240 ? imageTDTinyStyle : imageTDSmallStyle}>
                    <img src="https://static.affiliate.rakuten.co.jp/makelink/rl.svg"
                            style={this.props.image_size==240 ? rakutenImageStyleSmall : rakutenImageStyle} />
                    <div style={this.props.image_size==240 ? previewRakutenDivSmall : previewRakutenDiv}
                        ref={(el) => {if(el){ el.style.setProperty("color", "white", "important")}}}>
                        楽天で購入
                    </div>
                </div>
            </AnchorControl>
        );
    }
}

PopupAnchorControl.propTypes = {
    url_url: PropTypes.string,
    url_style: PropTypes.object,
    image_size: PropTypes.number,
}