
import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class OptionsControl extends Component {
    constructor(props){
      super(props);
      this.handleChange = this.handleChange.bind(this);
      this.option_selected = this.props.option_selected;
    }

    handleChange(e) {
      this.option_selected = e.target.attributes.value.value;
      this.props.onOptionChange(e.target.attributes.value.value);
    }

    render() {
      return (
        <div className={this.props.main_class}>
        {
          this.props.component_header ?
          <div className="component-header">{this.props.component_header}</div>
          : null
        }
            {this.props.option_list.map(((_option, i) => {
              return <label key={i}
                        onClick={this.handleChange}
                        value={_option.value}
                        className={this.option_selected==_option.value?"selected":""}>
                          {_option.text}
                    </label>
            }))}
        </div>
        );
    }
  }

OptionsControl.propTypes = {
  component_header: PropTypes.string,
  main_class: PropTypes.string,
  option_list: PropTypes.array,
  option_selected: PropTypes.string,
  onOptionChange: PropTypes.func
}