import React, { Component } from 'react';
import AnchorControl from './AnchorControl';
import PopupAnchorControl from './PopupAnchorControl';
import PropTypes from 'prop-types';
import {
    ANCHOR_CONTROL_CONTENT_MODE
    } from '../../../../../utilities/src/affiliate_const';

export default class PreviewPopupContentMixed extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const imageSize = parseInt(this.props.image_size_selected);
        const today = new Date()
        const _date_reference = today.getFullYear() + '/' + (today.getMonth() + 1) + '/' + today.getDate();

        const mainURLStyle = {
            wordWrap: "break-word",
            color: "#1d54a7",
        }
        const mainImageStyle = {
            margin: "2px",
        }
        const mainLinkDivStyle ={
            backgroundColor: "#FFFFFF",
            margin: "0px",
            padding: "5px",
            textAlign: "center",
            overflow: "hidden",

            border:"1px solid #95a5a6",
            borderRadius:"0.75rem",
            width: ((imageSize*2) + 24).toString() + "px"
        }
        const emptyPStyle = {
            color:"#000000",
            fontSize:"12px",
            lineHeight:"1.4em",
            margin:"5px",
            wordWrap:"break-word"
        }
        const itemNameLinkStyle ={
            fontSize: "12px",
            lineHeight: "1.4em",
            textAlign: "left",
            margin:"0px",
            padding:"2px 6px",
            wordWrap: "break-word",
        }

        const imageTDStyle = {
            width: (imageSize + 8).toString() + "px",
            verticalAlign: "top",
        }
        const imageTDDateStyle = {
            color:"#BBB",
        }
        const linkTDStyle = {
            width: imageSize.toString() + "px"
        }

        return (
            <table border="0" cellPadding="0" cellSpacing="0">
            <tbody>
                <tr>
                    <td>
                        <div style={mainLinkDivStyle}>
                            <table>
                                <tbody>
                                <tr>
                                    <td style={linkTDStyle}>
                                        <AnchorControl
                                            url_url={this.props.original_main_url}
                                            url_style={mainURLStyle}
                                            url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.IMAGE}
                                            url_content_image_url={this.props.main_image_url}
                                            url_content_image_style={mainImageStyle}
                                            url_content_image_alt="[商品価格に関しましては、リンクが作成された時点と現時点で情報が変更されている場合がございます。]"
                                            url_content_image_title="[商品価格に関しましては、リンクが作成された時点と現時点で情報が変更されている場合がございます。]"
                                            />
                                    </td>
                                    <td style={imageTDStyle}>
                                        <p style={itemNameLinkStyle}>
                                            <AnchorControl
                                                url_url={this.props.original_main_url}
                                                url_style={mainURLStyle}
                                                url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.TEXT}
                                                url_content_text_text={this.props.link_info.item_name}
                                                />
                                            <br />
                                            <span>価格：{this.props.link_info.item_price}円（{this.props.link_info.tax_flag==0?"税込":"税別"}、{this.props.link_info.post_flag==0?"送料無料":"送料別"})</span>
                                            <span style={imageTDDateStyle}>({_date_reference}時点)</span>
                                        </p>
                                        {
                                        (imageSize==400 || imageSize==300 || imageSize==240) ?
                                        <PopupAnchorControl
                                                url_url={this.props.modified_main_url}
                                                url_style={mainURLStyle}
                                                image_size={imageSize}
                                                />
                                        : null
                                        }
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                        <br />
                        <p style={emptyPStyle}></p>
                    </td>
                </tr>
            </tbody>
            </table>
        )
    }
}

PreviewPopupContentMixed.propTypes = {
    original_main_url: PropTypes.string,
    modified_main_url: PropTypes.string,
    image_size_selected: PropTypes.string,
    main_image_url: PropTypes.string,
    link_info: PropTypes.object
}