import React, { Component } from 'react';
import ReactDOMServer from 'react-dom/server';
import AnchorControl from './AnchorControl';
import PreviewPopupContentMixed from './PreviewPopupContentMixed';
import PropTypes from 'prop-types';
import {
    AFFILIATE_PREVIEW_CONTENT_MODE,
    ANCHOR_CONTROL_CONTENT_MODE
    } from '../../../../../utilities/src/affiliate_const';

export default class PreviewPopupContent extends Component {
    constructor(props) {
        super(props);
        this.link_preview_text = "";
    }

    componentDidMount(){
        if(this.props.mode == AFFILIATE_PREVIEW_CONTENT_MODE.TEXT) {
            this.props.previewTextComponent.current.value = ReactDOMServer.renderToStaticMarkup(this.link_preview_text);
        }
    }

    createUTCode(){
        const  _type = this.props.link_type_selected;
        const sourceJson = {
            'page': 'tool',
            'type': this.props.link_type_selected,
            'size': this.props.image_size_selected + "x" + this.props.image_size_selected,
            'nam':  _type=='picttext' ? 1 : 0,
            'namp': 'right',
            'com':  _type=='picttext' ? 1 : 0,
            'comp': 'down',
            'price': 1,
            'bor': 1,
            'col': 1,
            'bbtn': 1
        }
        return encodeURIComponent(btoa(JSON.stringify(sourceJson)));
    }

    render() {
        const imageSizeX2 = this.props.image_size_selected + "x" + this.props.image_size_selected;
        const mainURLStyle = {
            wordWrap: "break-word",
            color: "#1d54a7",
        }
        const mainImageStyle = {
            margin: "2px",
        }

        let modifiedMainURL =   this.props.modified_hybrid_link + "&link_type=" + this.props.link_type_selected + "&ut=" + this.createUTCode();
        let originalMainURL =   this.props.original_hybrid_link + "&link_type=" + this.props.link_type_selected + "&ut=" + this.createUTCode();
        let mainImageURLForEmbed    =   "https://hbb.afl.rakuten.co.jp/hgb/" + this.props.link_info.link_id_pc +"." + this.props.link_info.link_id_mb + "/?me_id=" + this.props.me_id+ "&item_id=" + this.props.item_id +
                            "&m=" + encodeURIComponent(this.props.image_url_selected) + "%3F_ex%3D80x80&pc=" + encodeURIComponent(this.props.image_url_selected) + "%3F_ex%3D" + imageSizeX2  +
                            "&s="+ imageSizeX2 + "&t=" + this.props.link_type_selected + (this.props.image_params ? this.props.image_params : "")
        let mainImageURLForPreview    =   this.props.image_url_selected + "?_ex=" + imageSizeX2;
        if(this.props.link_type_selected=="pict"){
            this.link_preview_text = (
                <AnchorControl
                    url_url={originalMainURL}
                    url_style={mainURLStyle}
                    url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.IMAGE}
                    url_content_image_url={this.props.mode == AFFILIATE_PREVIEW_CONTENT_MODE.TEXT?mainImageURLForEmbed:mainImageURLForPreview}
                    url_content_image_style={mainImageStyle}
                    />
                )
        }
        else if(this.props.link_type_selected=="text"){
            this.link_preview_text = (
                <AnchorControl
                    url_url={originalMainURL}
                    url_style={mainURLStyle}
                    url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.TEXT}
                    url_content_text_text={this.props.link_info.item_name}
                    />
            )
        }
        else if(this.props.link_type_selected=="picttext"){
            this.link_preview_text = (
                <PreviewPopupContentMixed
                    original_main_url={originalMainURL}
                    modified_main_url={modifiedMainURL}
                    image_size_selected={this.props.image_size_selected}
                    main_image_url={this.props.mode == AFFILIATE_PREVIEW_CONTENT_MODE.TEXT?mainImageURLForEmbed:mainImageURLForPreview}
                    link_info={this.props.link_info}
                    />
            )
        }
        //This text is to be shown in the text box which shows embeddable content as well as preview window.
        //This is to prevent action of users during creation of affiliate content do not affect tracking
        //data. We remove tracking data from the content created for preview popup but not from embedable
        //text. We need preview data as component and embeddable data as text.
        if(this.props.mode == AFFILIATE_PREVIEW_CONTENT_MODE.TEXT) {
            if(this.props.previewTextComponent.current) {
                this.props.previewTextComponent.current.value = ReactDOMServer.renderToStaticMarkup(this.link_preview_text);
                }
            return null;
        }
        else if(this.props.mode == AFFILIATE_PREVIEW_CONTENT_MODE.RENDER) {
            return this.link_preview_text;
        }
        return null;
    }
}

PreviewPopupContent.propTypes = {
    link_type_selected: PropTypes.string,
    image_url_selected: PropTypes.string,
    image_size_selected: PropTypes.string,
    link_info: PropTypes.object,
    modified_hybrid_link: PropTypes.string,
    original_hybrid_link: PropTypes.string,
    image_params: PropTypes.string,

    mode: PropTypes.string,

    me_id: PropTypes.number,
    item_id: PropTypes.number,
    pb_id: PropTypes.string,

    previewTextComponent: PropTypes.object
}