import React, { Component } from 'react';
import PropTypes from 'prop-types';

export default class AffiliateBarControls extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.closeButton.addEventListener("click", (e) => {
            this.props.onClose();
        });
        const arLinks =  Array.from(document.querySelectorAll('.bar-controls-div > a'));
        arLinks.forEach(function(barControlAnchor){
            barControlAnchor.addEventListener('click', () => {
                return true;
            });
        })
    }

    render() {
        const barControlsDivClasses = 'component-container bar-controls-div';
        // const closeButtonGoogleAnalytics = 'Pck_affiliate_close_button_' + this.props.tu_id;
        return (
            <div className={barControlsDivClasses}>
                <a href="https://affiliate.rakuten.co.jp/report" target="_blank" rel="nofollow noopener noreferrer">
                    <img src="/img/affiliate_survey.svg" width="18" height="18" />
                        レポート<br />を見る
                </a>
                <a href="https://affiliate.rakuten.co.jp/guides/begin/" target="_blank" rel="nofollow noopener noreferrer">
                    <img src="/img/affiliate_new_user.svg" width="18" height="18" />
                        初心者<br />ガイド
                </a>
                <a href="https://affiliate.faq.rakuten.net/" target="_blank" rel="nofollow noopener noreferrer">
                    <img src="/img/affiliate_help_outline.svg" width="18" height="18" />
                    ヘルプ
                </a>
                <a ref={c => this.closeButton = c} className="no-close-anchor">
                    <img src="/img/affiliate_close.png" width="18" height="18" className="no-close-image" />
                </a>
            </div>
        );
    }
}

AffiliateBarControls.propTypes = {
    tu_id: PropTypes.string,
    onClose: PropTypes.func
}
