import React, { Component } from 'react';
import PropTypes from 'prop-types';
import 'url-polyfill'
import OptionsControl from './OptionsControl';
import AnchorControl from './AnchorControl';
import ItemImageSlider from './ItemImageSlider';
import PreviewPopup from './PreviewPopup';
import AffiliateBarControls from './AffiliateBarControls';
import PreviewPopupContent from './PreviewPopupContent';
import {validateRuntimeOnUserInteraction} from '../../../../../utilities/src/runtime_helper';

import {
    AFFILIATE_PREVIEW_CONTENT_MODE,
    ANCHOR_CONTROL_CONTENT_MODE
    } from '../../../../../utilities/src/affiliate_const';

export default class AffiliateBar extends Component {
    constructor(props) {
        super(props);
        this.ppRef = React.createRef();
        this.handleImageUrlChange = this.handleImageUrlChange.bind(this);
        this.handleLinkTypeChange = this.handleLinkTypeChange.bind(this);
        this.handleImageSizeChange = this.handleImageSizeChange.bind(this);
        this.handleBarEvents = this.handleBarEvents.bind(this);
        this.handlePreviewPopupClose = this.handlePreviewPopupClose.bind(this);
        this.handleCloseButtonClick = this.handleCloseButtonClick.bind(this);

        this.copyEmeddableDataToClipboard = this.copyEmeddableDataToClipboard.bind(this);
        //Add scid parameters to pc and m url's inside hybrid url property
        //This does not work for older version of Edge. We are using url-polyfill
        // to support them. Works with versions since Apr '18
        this.original_hybrid_link = this.props.data.hybrid_link;
        let hybrid_url = new URL(this.props.data.hybrid_link);
        let pc_url_string = decodeURIComponent(hybrid_url.searchParams.get('pc'));
        let decoded_pc_url = new URL(pc_url_string);
        decoded_pc_url.searchParams.append('scid', 'af_pc_bbtn');

        hybrid_url.searchParams.delete('pc');
        hybrid_url.searchParams.append('pc', decoded_pc_url.toString());
        this.modified_hybrid_link = hybrid_url.toString();

        this.props.data.link_info.image_url = 'https:'+ this.props.data.link_info.image_url;
        if(this.props.data.link_info.alt_image_urls.length > 0) {
            if(!this.props.data.link_info.alt_image_urls.includes(this.props.data.link_info.image_url)) {
                this.props.data.link_info.alt_image_urls.unshift(this.props.data.link_info.image_url);
            }
        }
        else {
            this.props.data.link_info.alt_image_urls.unshift(this.props.data.link_info.image_url);
        }

        this.state = {
                        image_url_selected: this.props.data.link_info.alt_image_urls[0],
                        link_type_selected:'picttext',
                        image_size_selected: '400',
                        preview_visible: false
                    };
    }

    //The server return a default 1x1 image in some cases.
    //These images need to be removed from the list
	componentDidMount(){
        let slider_div = document.getElementsByClassName("slider-div")[0];
        let slider_images = slider_div.getElementsByTagName("img");
        let imagesModified = false;
		for(let j = slider_images.length - 1; j >= 0; j--){
            let _slider_img = slider_images[j];
            //natural width is actual width of source image
			var width = _slider_img.naturalWidth;
            var height = _slider_img.naturalHeight;

            if(width==1 || height==1){
                const img_index = this.props.data.link_info.alt_image_urls.indexOf(_slider_img.src);
                if(img_index != -1) {
                    this.props.data.link_info.alt_image_urls.splice(img_index, 1);
                }
                imagesModified = true;
			}
        }
        //If any empty images, reset the state.
        if(imagesModified){
            this.setState({image_url_selected: this.props.data.link_info.alt_image_urls[0]});
        }
	}

    handleImageUrlChange(_image_url_selected) {
        this.setState({image_url_selected: _image_url_selected});
    }

    handleLinkTypeChange(_link_type_selected) {
        this.setState({link_type_selected: _link_type_selected});
    }

    handleImageSizeChange(_image_size_selected) {
        this.setState({image_size_selected: _image_size_selected});
    }

    setPreviewOverlay() {
        validateRuntimeOnUserInteraction(() => {
            this.setState({preview_visible: true});
            window.parent.postMessage({
                method: "RTBcs.AffiliateManager.overlayDivSet"
            }, '*');
        });
    }
    clearPreviewOverlay(){
        validateRuntimeOnUserInteraction(() => {
            this.setState({preview_visible: false});
            window.parent.postMessage({
                method: "RTBcs.AffiliateManager.overlayDivReset"
            }, '*');
        });
    }
    handlePreviewPopupClose() {
        this.clearPreviewOverlay();
    }
    handleBarEvents(_button_value){
        if(_button_value == "Preview"){
            if(this.state.preview_visible){
                this.clearPreviewOverlay();}else{this.setPreviewOverlay();
                }
        }
        else if(_button_value == "Copy"){
            this.copyEmeddableDataToClipboard();
        }
    }

    handleCloseButtonClick() {
        validateRuntimeOnUserInteraction(() => {
            window.parent.postMessage({method:"RTBcs.AffiliateManager.closeToolbar"}, '*');
        });
    }

    copyEmeddableDataToClipboard(){
        validateRuntimeOnUserInteraction(() => {
          this.ppRef.current.select();
          try {
            document.execCommand("copy");  // Security exception may be thrown by some browsers.
          } catch (ex) {
            console.warn("Copy to clipboard failed.", ex);
          }
        });
    }
    render() {
        return (
            <React.Fragment>
                <AnchorControl
                    url_url="https://affiliate.rakuten.co.jp"
                    url_class="component-container logo-div"
                    url_content_mode={ANCHOR_CONTROL_CONTENT_MODE.IMAGE}
                    url_content_image_url="/img/affiliatebar_logo.png"
                    onComponentMount={() => {}}
                    />
                <OptionsControl
                    component_header="リンクタイプを選択"
                    main_class="component-container link-type-div"
                    option_list={[
                        {'text':'テキスト', 'value':'text'},
                        {'text':'画像', 'value':'pict'},
                        {'text':'テキスト＋画像', 'value':'picttext'}
                    ]}
                    option_selected={this.state.link_type_selected}
                    onOptionChange={this.handleLinkTypeChange}
                    />
                <ItemImageSlider
                    alt_image_urls={this.props.data.link_info.alt_image_urls}
                    image_url_selected={this.state.image_url_selected}
                    onSelectedImageChange={this.handleImageUrlChange}
                    />
                <OptionsControl
                    component_header="画像サイズを選択"
                    main_class="component-container image-size-div"
                    option_list={[
                        {'text':'400x400', 'value':'400'},
                        {'text':'300x300', 'value':'300'},
                        {'text':'240x240', 'value':'240'},
                        {'text':'128x128', 'value':'128'},
                        {'text':'80x80', 'value':'80'},
                        {'text':'64x64', 'value':'64'}
                    ]}
                    option_selected={this.state.image_size_selected}
                    onOptionChange={this.handleImageSizeChange}
                    />
                 <textarea name="txtEmbedPreview" rows="2" cols="40"
                    readOnly={true} style={{fontSize: "12px", margin:"12px 5px 0px 10px", width:"300px", height:"52px"}}
                    ref={this.ppRef} >
                </textarea>
                <PreviewPopupContent
                    link_type_selected={this.state.link_type_selected}
                    image_url_selected={this.state.image_url_selected}
                    image_size_selected={this.state.image_size_selected}
                    link_info={this.props.data.link_info}
                    modified_hybrid_link={this.modified_hybrid_link}
                    original_hybrid_link={this.original_hybrid_link}
                    image_params={this.state.image_params}

                    me_id={this.props.data.me_id}
                    item_id={this.props.data.item_id}
                    pb_id={this.props.data.pb_id}

                    mode={AFFILIATE_PREVIEW_CONTENT_MODE.TEXT}
                    previewTextComponent={this.ppRef}
                >
                </PreviewPopupContent>

                <OptionsControl
                    component_header=""
                    main_class="component-container preview-div"
                    option_list={[
                        {'text':'プレビュー', 'value':'Preview'},
                        {'text':'コピー', 'value':'Copy'}
                    ]}
                    onOptionChange={this.handleBarEvents}
                    />
                <PreviewPopup
                    preview_visible={this.state.preview_visible}
                    tu_id={this.props.data.tu_id}
                    onPreviewPopupClose={this.handlePreviewPopupClose}
                    onCopyButtonClick={() => {}}
                    >
                    <PreviewPopupContent
                        link_type_selected={this.state.link_type_selected}
                        image_url_selected={this.state.image_url_selected}
                        image_size_selected={this.state.image_size_selected}
                        link_info={this.props.data.link_info}
                        modified_hybrid_link={this.modified_hybrid_link}
                        original_hybrid_link={this.original_hybrid_link}

                        me_id={this.props.data.me_id}
                        item_id={this.props.data.item_id}
                        pb_id={this.props.data.pb_id}

                        mode={AFFILIATE_PREVIEW_CONTENT_MODE.RENDER}
                    />
                </PreviewPopup>

                <AffiliateBarControls
                    tu_id={this.props.data.tu_id}
                    onClose={this.handleCloseButtonClick}
                    />
            </React.Fragment>
        );
    }
}

AffiliateBar.propTypes = {
    data: PropTypes.object
}