import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
    ANCHOR_CONTROL_CONTENT_MODE
    } from '../../../../../utilities/src/affiliate_const';

export default class AnchorControl extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount(){
        if(this.props.onComponentMount){
            this.props.onComponentMount();
        }
    }

    render() {
        return (
                <a  href={this.props.url_url} target="_blank" rel="nofollow noopener noreferrer"
                    style={this.props.url_style} className={this.props.url_class}>
                    {
                        this.props.url_content_mode == ANCHOR_CONTROL_CONTENT_MODE.TEXT ? this.props.url_content_text_text : null
                    }
                    {
                        this.props.url_content_mode == ANCHOR_CONTROL_CONTENT_MODE.IMAGE ?
                        <img src={this.props.url_content_image_url} style={this.props.url_content_image_style}
                            className={this.props.url_content_image_class}
                            alt={this.props.url_content_image_alt}
                            title={this.props.url_content_image_title} border="0"  />
                        : null
                    }
                    {
                        this.props.url_content_mode == ANCHOR_CONTROL_CONTENT_MODE.CHILDREN ?
                        this.props.children
                        : null
                    }
                </a>
        );
    }
}

AnchorControl.propTypes = {
    url_url: PropTypes.string,
    url_style: PropTypes.object,
    url_class: PropTypes.string,

    url_content_mode: PropTypes.string,

    url_content_image_url: PropTypes.string,
    url_content_image_style: PropTypes.object,
    url_content_image_class: PropTypes.string,
    url_content_image_alt: PropTypes.string,
    url_content_image_title: PropTypes.string,

    url_content_text_text: PropTypes.string,

    children: PropTypes.any,

    onComponentMount: PropTypes.func
}