import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
    AFFILIATE_COPY_BUTTON_GOOGLE_ANALYTICS_CODES
} from '../../../../../utilities/src/affiliate_const';

export default class PreviewPopup extends Component {
    constructor(props) {
        super(props);

        this.handleCopyButtonClick = this.handleCopyButtonClick.bind(this);
        this.handlePopupCloseClick = this.handlePopupCloseClick.bind(this);
    }

    handleCopyButtonClick() {
        this.props.onCopyButtonClick(AFFILIATE_COPY_BUTTON_GOOGLE_ANALYTICS_CODES.POPUP + "_" + this.props.tu_id);
    }
    handlePopupCloseClick() {
        this.props.onPreviewPopupClose();
    }

    render() {
        const previewPreviewDivClasses = 'center-div preview-preview-div' + (this.props.preview_visible ? ' show' : ' hide');
        const closeImageStyle = {
            cursor: "pointer"
        }
        return (
            <div className={previewPreviewDivClasses}>
                <div className="preview-preview-header">
                    プレビュー
                    <div style={{float:"right", display:"inline-block"}}>
                        <img src="/img/affiliate_close.png" width="18" height="18" style={closeImageStyle} onClick={this.handlePopupCloseClick}/>
                    </div>
                </div>
                {this.props.children}
                <div className="preview-button-div">
                    <label onClick={this.handleCopyButtonClick}>コピー</label>
                </div>
            </div>
        );
    }
}

PreviewPopup.propTypes = {
    preview_visible: PropTypes.bool,
    tu_id: PropTypes.string,
    children: PropTypes.any,

    onPreviewPopupClose: PropTypes.func,
    onCopyButtonClick: PropTypes.func
}