import React, { Component } from "react";
import Slider from "react-slick";
import PropTypes from 'prop-types';

export default class ItemImageSlider extends Component {
	constructor(props) {
		super(props);
		this.image_url_selected = this.props.image_url_selected;
		this.handleChange = this.handleChange.bind(this);
	}

	handleChange(e) {
		this.image_url_selected = e.target.attributes.src.value;
		this.props.onSelectedImageChange(e.target.attributes.src.value);
	}

	render() {
		const settings = {
			arrows: true,
			dots: false,
			infinite: true,
			speed: 500,
			slidesToShow: Math.min(2, this.props.alt_image_urls.length),
			slidesToScroll: 2,
			autoplay: false,
			variableWidth: false
		}

		const sliderDivClasses = 'component-container slider-div';
		return (
			<div id="slider_div" className={sliderDivClasses}>
				<div className="component-header">画像を選択</div>
				<Slider {...settings}>
					{this.props.alt_image_urls.map(((_image_url, i) => {
						return <img
								key={i}
								src={_image_url}
								className={this.image_url_selected==_image_url ? "slider-image-border" : "slider-image-border-default"}
								onClick={this.handleChange}
							/>
					}))}
				</Slider>
			</div>
		);
	}
}

ItemImageSlider.propTypes = {
	alt_image_urls: PropTypes.array,
	image_url_selected: PropTypes.string,
	onSelectedImageChange: PropTypes.func
}